import { CSSProperties } from "react";

import { IconName } from "../Icon/Icon";

export type ToastVariant = "Default" | "Warning" | "Error" | "Validation";

export interface StyledToastProps {
	variant?: ToastVariant
	trailingButtonWidth?: number
	iconName?: IconName
	iconColor?: string
	textColor?: string
	backgroundColor?: string
	$zIndex?: CSSProperties["zIndex"]
}

export interface ToastModalProps extends StyledToastProps {
	modalTitle?: string
	modalDetails?: string
	modalButtonText?: string
}
export interface ToastProps extends ToastModalProps, StyledToastProps {
	dataQA: string
	className?: string
	text?: string
	showIcon?: boolean
	onDismiss?: () => void;
	/** @description callback for when the toast timeout has finished */
	onTimeout?: () => void;
	trailingLinkText?: string
	trailingLinkAction?: () => void
	timeout?: number
	hasTimeout?: boolean
}

export interface Notification extends ToastProps {
    id: string;
}
  
export interface ToastContextType {
	notifications: Notification[];
	appendNotification: (notification: Omit<Notification, "id">) => void;
	removeNotification: (id: string) => void;
}

const APPEND_NOTIFICATION = "APPEND_NOTIFICATION";
const REMOVE_NOTIFICATION = "REMOVE_NOTIFICATION";

export type ToastAction =
  | { type: typeof APPEND_NOTIFICATION; payload: Notification }
  | { type: typeof REMOVE_NOTIFICATION; payload: string };

export interface ToastNotificationProps {
    notification: Notification;
}
